<template>
    <div class="details">
      <all_titel :indexText="indexText"></all_titel>
      <div class="detailsCenter">
         <div class="detailTop">
            <div class="topLeft">
              <magnifier :url="detailsList.url"></magnifier>
            </div>
            <div class="topRight">
               <ul>
                 <li>名称： {{detailsList.name}}/{{detailsList.explain}}</li>
                 <li>分类： {{detailsList.classification}}</li>
                 <li>颜色： {{detailsList.color}}</li>
                 <li>简介： {{detailsList.synopsis}}</li>
               </ul>
            </div>
         </div>
         <div class="detailBottom">
           <div class="bottomLeft">
              <div class="title">产品详情</div>
              <div class="detailsList">
                 <div class="proType">
                    <div class="type-title">
                       产品属性
                    </div>
                    <div class="type-item">
                       <div class="item-text">
                         <span class="name">名称:</span><span class="text">{{detailsList.name}}/{{detailsList.explain}}</span>
                       </div>
                      <div class="item-text">
                         <span class="name">分类:</span><span class="text">{{detailsList.classification}}</span>
                       </div>
                      <div class="item-text">
                         <span class="name">颜色:</span><span class="text">{{detailsList.color}}</span>
                       </div>
                      <div class="item-text">
                         <span class="name">尺寸（毫米）:</span><span class="text">{{detailsList.size}}</span>
                       </div>
                      <div class="item-text">
                         <span class="name">风格:</span><span class="text">{{detailsList.style}}</span>
                       </div>
                      <div class="item-text">
                         <span class="name">材质:</span><span class="text">{{detailsList.material}}</span>
                       </div>
                      <div class="item-text">
                         <span class="name">产品定位:</span><span class="text">{{detailsList.location}}</span>
                       </div>
                      <div class="item-text">
                         <div class="name" style="width: 50px">简介:</div><div class="text">{{detailsList.synopsis}}</div>
                       </div>
                    </div>
                 </div>
                 <div class="proType">
                    <div class="type-title">
                       产品服务
                    </div>
                    <div class="type-item">
                       <div class="item-text">
                         <span class="name">备注:</span><span class="text">如您对此产品有兴趣请致电我们的工作人员咨询。</span>
                       </div>
                      <div class="item-text">
                         <span class="name">电话:</span><span class="text">15537557010</span>
                       </div>
                      <div class="item-text">
                        <span class="name">固话:</span><span class="text">0375-7088900</span>
                      </div>
                      <div class="item-text">
                         <span class="name">上班时间:</span><span class="text">上午8点至晚上10点，周一至周日全天。</span>
                      </div>
                    </div>
                 </div>
              </div>
           </div>
           <div class="bottomRight">
             <div class="title">猜你喜欢</div>
              <div class="proList">
                <div class="proList">
                  <ul>
                    <li class="list-item" v-for="item in detailsList.guessLikeList" @click="jumpToDetails(item.id)">
                      <div class="listImg">
                        <img :src="item.url" alt="">
                      </div>
                      <div class="listText">
                        {{item.name}}
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
           </div>
         </div>
      </div>
    </div>
</template>

<script>
  import all_titel from '@/components/pageNavigation/pageNavigation'
  import magnifier from '@/components/magnifier/magnifier'
    export default {
      name: "index",
      components:{
        all_titel,
        magnifier
      },
      props:{
        proList:{
          type:Object
        }
      },
      data(){
          return{
            detailsList:{},
            isActive:false,
            activeClass:'activeClass',
            errorClass:'errorClass',
          }
      },
      computed:{
        indexText(){
          return this.$store.state.indexText;
        }
      },
      created(){
        let id = this.$route.query.id;
        this.$store.dispatch('listenTOAllTitle',['首页','产品平台','产品详情']);
        this.getDetails(id);
      },
      methods:{
        jumpToDetails(val){
          this.$router.push({
            path: '/details',
            query: {
              id: val
            }
          });
          $('body,html').animate({
            scrollTop:0
          },500);
          this.getDetails(val)
        },
        getDetails(id){
          for(let i=0;i<this.proList.dataDetails.length;i++){
            if(this.proList.dataDetails[i].id===id){
              this.detailsList = this.proList.dataDetails[i];
              break;
            }
          }
        }
      }
    }
</script>

<style scoped>
.details{
  width: 960px;
  margin: 10px auto 0 auto;
  overflow: hidden;
}
.detailsCenter{
  width: 100%;
  margin-top: 30px;
  margin-bottom: 50px;
}
  .detailTop{
    width: 100%;
    display: flex;
  }
  .topLeft{
    flex: 0 0 420px;
    overflow: hidden;
    margin-right: 50px;
  }
  .topLeft img{
    width: 420px;
    cursor: pointer;
    display: block;
  }
  .topRight{
    flex: 1;
  }
  .topRight ul{
    margin: 0;
    padding: 0;
    list-style-type: none;
  }
  .topRight ul li{
    font-size: 14px;
    color: #666666;
    margin: 8px 0;
  }

  .detailBottom{
    width: 100%;
    margin-top: 20px;
    display: flex;
  }
  .bottomLeft{
    margin-right: 50px;
    flex: 0  0 420px;
  }
  .title{
    width: 96%;
    padding: 6px 2%;
    font-size: 16px;
    color: saddlebrown;
    background-color: #ccc;
  }
  .bottomRight{
    flex: 1;
  }
  .detailsList{
    width: 100%;
    margin-top: 15px;
  }
  .proType{
    width: 100%;
    margin-bottom: 15px;
    overflow: hidden;
  }
  .type-title{
    width: 100%;
    font-size: 16px;
    padding-bottom: 8px;
    margin-bottom: 10px;
    border-bottom: 1px solid #cccccc;
  }
  .type-item{
    width: 100%;
    overflow: hidden;
  }
  .item-text{
    width: 100%;
    display: flex;
    margin-bottom: 5px;
  }
  .name{
    color: #333;
    display: inline-block;
    font-size: 14px;
    font-weight: bold;
  }
  .text{
    color: #666666;
    display: inline-block;
    font-size: 14px;
    margin-left: 5px;
  }
.proList{
  width: 100%;
  margin-top: 20px;
  overflow: hidden;
}
.list-item{
  width: 230px;
  float: left;
  overflow: hidden;
  border: 1px solid #f4f4f4;
  margin-right: 12px;
  margin-bottom: 20px;
  box-sizing: border-box;
  cursor: pointer;
}
.list-item:nth-child(4n){
  margin-right: 0;
}
.listImg{
  width: 100%;
  overflow: hidden;
}
.listImg img{
  width: 100%;
  height: 230px;
  display: block;
  transition: all .6s;
}
.list-item:hover img{
  transform: scale(1.4);
}
.list-item:hover{
  border: 1px solid saddlebrown;
  box-sizing: border-box;
}
.listText{
  width: 100%;
  padding: 10px 0;
  font-size: 14px;
  color: #666666;
  text-align: center;
  background-color: #f4f4f4;
}
  /*移动端*/
  @media screen and (max-width: 768px){
    .details{
      width: 94%;
      margin: 10px auto 0 auto;
      overflow: hidden;
    }
    .detailsCenter{
      width: 100%;
      margin-top: 15px;
      margin-bottom: 10px;
    }
    .detailTop{
      width: 100%;
      display: flex;
    }
    .topLeft{
      flex: 0 0 140px;
      overflow: hidden;
      margin-right: 10px;
    }
    .topLeft img{
      width: 120px;
      display: block;
      cursor: pointer;
    }
    .topRight{
      flex: 1;
    }
    .topRight ul{
      margin: 0;
      padding: 0;
      list-style-type: none;
    }
    .topRight ul li{
      margin:0 0  5px 0;
      font-size: 12px;
    }

    .detailBottom{
      width: 100%;
      margin-top: 20px;
      display: block;
    }
    .bottomLeft{
      margin-right: 0;
    }
    .title{
      width: 96%;
      padding: 6px 2%;
      font-size: 16px;
      color: saddlebrown;
      background-color: #ccc;
    }
    .bottomRight{
      margin-right: 0;
    }
    .detailsList{
      width: 100%;
      margin-top: 15px;
    }
    .proType{
      width: 100%;
      margin-bottom: 15px;
      overflow: hidden;
    }
    .type-title{
      width: 100%;
      font-size: 14px;
      padding-bottom: 8px;
      margin-bottom: 10px;
      border-bottom: 1px solid #cccccc;
    }
    .type-item{
      width: 100%;
      overflow: hidden;
    }
    .item-text{
      width: 100%;
      display: flex;
      margin-bottom: 5px;
    }
    .name{
      color: #333;
      display: inline-block;
      font-size: 12px;
      font-weight: bold;
    }
    .text{
      color: #666666;
      display: inline-block;
      font-size: 12px;
      margin-left: 5px;
    }
    .proList{
      width: 100%;
      margin-top: 10px;
      overflow: hidden;
    }
    .list-item{
      width:49%;
      float: left;
      overflow: hidden;
      border: 1px solid #f4f4f4;
      margin-right: 2%;
      margin-bottom: 10px;
      box-sizing: border-box;
      cursor: pointer;
    }
    .list-item:nth-child(2n){
      margin-right: 0;
    }
    .listImg{
      width: 100%;
      overflow: hidden;
    }
    .listImg img{
      width: 100%;
      height: 165px;
      display: block;
      transition: all .6s;
    }
    .listText{
      width: 100%;
      padding: 8px 0;
      font-size: 12px;
      color: #666666;
      text-align: center;
      background-color: #f4f4f4;
    }
  }
</style>




